    import utilsJs, {getYearFormat,getMonthFormat,getDateFormat,getDatetimeFormat} from "../../../utils/utils.js";
    export default {
        data() {
            return {
                addEditForm:null,
                id: '',
                type: '',
                sessionTable : "",//登录账户所在表名
                role : "",//权限
                userId:"",//当前登录人的id
                jiaoshiForm: {},
                zhuanyeForm: {},
                ro:{
                    kechengName: false,
                    kechengTypes: false,
                    kechengPhoto: false,
                    jiaoshiId: false,
                    zhuanyeId: false,
                    kechengShangke: false,
                    kechengShichang: false,
                    zhuanyeTypes: false,
                    zhuanyeErjiTypes: false,
                    kechengContent: false,
                    kechengDelete: false,
                    insertTime: false,
                },
                ruleForm: {
                    kechengName: '',
                    kechengTypes: '',
                    kechengPhoto: '',
                    jiaoshiId: '',
                    zhuanyeId: '',
                    kechengShangke: '',
                    kechengShichang: '',
                    zhuanyeTypes: '',
                    zhuanyeErjiTypes: '',
                    kechengContent: '',
                    kechengDelete: '',
                    insertTime: '',
                },
                kechengTypesOptions : [],
                zhuanyeTypesOptions : [],
                zhuanyeErjiTypesOptions : [],
                jiaoshiOptions : [],
                zhuanyeOptions : [],
                rules: {
                   kechengName: [
                              { required: true, message: '课程名称不能为空', trigger: 'blur' },
                          ],
                   kechengTypes: [
                              { required: true, message: '课程类型不能为空', trigger: 'blur' },
                              {  pattern: /^[0-9]*$/,
                                  message: '只允许输入整数',
                                  trigger: 'blur'
                              }
                          ],
                   kechengPhoto: [
                              { required: true, message: '课程封面不能为空', trigger: 'blur' },
                          ],
                   jiaoshiId: [
                              { required: true, message: '上课教师不能为空', trigger: 'blur' },
                              {  pattern: /^[0-9]*$/,
                                  message: '只允许输入整数',
                                  trigger: 'blur'
                              }
                          ],
                   zhuanyeId: [
                              { required: true, message: '专业信息不能为空', trigger: 'blur' },
                              {  pattern: /^[0-9]*$/,
                                  message: '只允许输入整数',
                                  trigger: 'blur'
                              }
                          ],
                   kechengShangke: [
                              { required: true, message: '上课时间不能为空', trigger: 'blur' },
                          ],
                   kechengShichang: [
                              { required: true, message: '课程时长不能为空', trigger: 'blur' },
                          ],
                   zhuanyeTypes: [
                              { required: true, message: '学校不能为空', trigger: 'blur' },
                              {  pattern: /^[0-9]*$/,
                                  message: '只允许输入整数',
                                  trigger: 'blur'
                              }
                          ],
                   zhuanyeErjiTypes: [
                              { required: true, message: '学院不能为空', trigger: 'blur' },
                              {  pattern: /^[0-9]*$/,
                                  message: '只允许输入整数',
                                  trigger: 'blur'
                              }
                          ],
                   kechengContent: [
                              { required: true, message: '课程简介不能为空', trigger: 'blur' },
                          ],
                   kechengDelete: [
                              { required: true, message: '逻辑删除不能为空', trigger: 'blur' },
                              {  pattern: /^[0-9]*$/,
                                  message: '只允许输入整数',
